.f-input {
  position: relative;
  font-size: inherit;
  text-align: left; }
  .f-input *,
  .f-input *:before,
  .f-input *:after {
    box-sizing: border-box; }
  .f-input__inner {
    display: block;
    position: relative; }
  .f-input__field {
    color: inherit;
    padding: 1em 0.9em;
    width: 100%;
    border: 0;
    caret-color: #6fcf97;
    font-size: 1em;
    font-family: inherit; }
    .f-input__field:focus {
      outline: 0; }
    .f-input__field.f-input__field[disabled] {
      color: silver; }
    .has-error .f-input__field {
      caret-color: #d33944; }
    .f-input__field::-moz-selection {
      color: white;
      background: #6fcf97 !important;
      text-shadow: none; }
    .f-input__field::selection {
      color: white !important;
      background: #6fcf97 !important;
      text-shadow: none; }
    .has-error .f-input__field::-moz-selection {
      color: white;
      background: #d33944 !important;
      text-shadow: none; }
    .has-error .f-input__field::selection {
      color: white !important;
      background: #d33944 !important;
      text-shadow: none; }
    .f-input__field:-webkit-autofill {
      -webkit-text-fill-color: inherit !important; }
  .f-input__label {
    user-select: none;
    font-size: 1em; }
  .f-input__label-text {
    color: gray; }
    .f-input__field[disabled].f-input__field[disabled] + .f-input__label .f-input__label-text {
      color: silver; }
  .f-input__indicator {
    pointer-events: none;
    user-select: none;
    color: silver; }
    .f-input__indicator--optional {
      font-size: 0.5em; }
    .f-input__indicator--required {
      font-size: 1em; }
    .has-error .f-input__field:hover ~ .f-input__indicator,
    .has-error .f-input__field:focus ~ .f-input__indicator {
      color: #d33944 !important; }
    .f-input__field[disabled].f-input__field[disabled] ~ .f-input__indicator {
      color: silver; }
  .f-input__message {
    font-size: 0.8em; }
    .f-input__message--instruction {
      color: gray; }
    .f-input__message--error {
      color: #d33944; }
      .has-error .f-input__message--error::-moz-selection {
        color: white;
        background: #d33944 !important;
        text-shadow: none; }
      .has-error .f-input__message--error::selection {
        color: white !important;
        background: #d33944 !important;
        text-shadow: none; }
    .f-input__message--error + .f-input__message--instruction {
      display: none; }
